:root {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}


body {

  height: 100vh;
  width: 100vw;
  font-family: sans-serif;
  cursor: pointer;
  margin-bottom: 10px;
}

p{
  margin: 0;
}

.top{
  width: 100%;
  margin: 0 0 5px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.data-entry {
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: flex-start;
}


.limits{
  display: flex;
  flex-flow: column;
  align-items: center;
  width: 100%;
}


.limits div, .data-entry div{
    display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: flex-start;
  margin: 3px 0;
  justify-content: space-between;


}

.num-input{
  width: 150px;
  /*height: 150px;*/
  display: flex;
  align-content: center;
  text-align: center;
  font-size: 1.1rem;
}

input[type=text] {
  min-width: 10%;
}
.limits .submit {
  margin: 8px 0;
  width: 120px;
  height: 50px;

  font-family: Arial Narrow;
  font-size: 1.4rem;
  text-decoration: uppercase;
  box-shadow: none;
}

/*Roll call*/
.register {
  display: flex;
  flex-flow: column;
  align-items: center;
}
.present,  .absent {
  width: 80%;
  text-align: center;
  padding: 5px;
  margin: 2px 0;
}
.present {
  background:  #c0ffb0;
  background-size: cover;
  color:  #1b0d6d; 
}

.absent {
  background: #d71515 ;
  background-size: cover;
  color:#f3f8f7;
}

#roll .buttons {
  display: flex;
  flex-flow: row;
  align-items: center;
  justify-content: center;
}

#roll .buttons button {
  margin: 8px 0;
  width: 120px;
  height: 50px;

  font-family: Arial Narrow;
  font-size: 1.4rem;
  text-decoration: uppercase;
  box-shadow: none;
}

#roll .buttons #submit {
  background: #9815a7 ;
  color:#c9dafb;
}

.final {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 10px;

}

.final div{
  max-width: 80%;
  display: flex;
  flex-flow: column;
  align-items: flex-start;
  justify-content: flex-start;
}